import React from 'react';
import Link from 'gatsby-link';
import moment from 'moment';
import Disqus from '../Disqus/Disqus';
import rehypeReact from "rehype-react";
import './style.scss';

class LazyBase extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      module: null
    };
  }

  render() {
    let reactclass = this.props.reactclass ? this.props.reactclass : 'default';

    if (this.state.module) {
      const Component = this.state.module[reactclass];
      return <Component {...this.props}/>;
    } else {
      return null;
    }
  }
}

class Demon extends LazyBase {
  componentDidMount() {
    require.ensure([], (require) => {
      let module = require('ws-demon');
      this.setState({module: module});
    }, 'ws-demon');
  }
}

class Colormap extends LazyBase {
  componentDidMount() {
    require.ensure([], (require) => {
      let module = require('ws-colormap');
      this.setState({module: module});
    }, 'ws-colormap');
  }
}

class MunkerIllusion extends LazyBase {
  componentDidMount() {
    require.ensure([], (require) => {
      let module = require('ws-munker-illusion');
      this.setState({module: module});
    }, 'ws-munker-illusion');
  }
}


class PostTemplateDetails extends React.Component {
  render() {
    const { subtitle, author, copyright } = this.props.data.site.siteMetadata;
    const post = this.props.data.markdownRemark;
    const tags = post.fields.tagSlugs;

    let components = {};
    if (typeof window !== 'undefined') {
      components['blog-demon'] = Demon;
      components['blog-colormap'] = Colormap;
      components['blog-munker-illusion'] = MunkerIllusion;
    }
    const renderAst = new rehypeReact({
       createElement: React.createElement,
       components: components
    }).Compiler;

    const homeBlock = (
      <div>
        <Link className="post-single__home-button" to="/">All Articles</Link>
      </div>
    );

    const tagsBlock = (
      <div className="post-single__tags">
        <ul className="post-single__tags-list">
          {tags && tags.map((tag, i) => (
            <li className="post-single__tags-list-item" key={tag}>
              <Link to={tag} className="post-single__tags-list-item-link">
                {post.frontmatter.tags[i]}
              </Link>
            </li>
          ))}
        </ul>
      </div>
    );

    const commentsBlock = (
      <div>
        <Disqus postNode={post} siteMetadata={this.props.data.site.siteMetadata} />
      </div>
    );

    return (
      <div>
        {homeBlock}
        <div className="post-single">
          <div className="post-single__inner">
            <h1 className="post-single__title">{post.frontmatter.title}</h1>
            <div className="post-single__body">
            { /*
            <div dangerouslySetInnerHTML={{ __html: post.html }} />
            */ }
            {
               renderAst(post.htmlAst)
            }
            </div>
            <div className="post-single__date">
              <em>Published {moment(post.frontmatter.date).format('D MMM YYYY')}</em>
            </div>
          </div>
          <div className="post-single__footer">
            {tagsBlock}
            <hr />
            <p className="post-single__footer-text">
              { /*
              {subtitle}              
              <a href={`https://twitter.com/${author.twitter}`} target="_blank" rel="noopener noreferrer">
                <br /> <strong>{author.name}</strong> on Twitter
              </a>
              */ }
              Comments?  I'd love to hear them.  Send them
              to <a href="mailto:dkirkman@gmail.com">dkirkman@gmail.com</a>
              <br/>
              <p className="sidebar__copyright">
                 {copyright}
              </p>
            </p>
            {commentsBlock}
          </div>
        </div>
      </div>
    );
  }
}

export default PostTemplateDetails;



